@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@font-face{font-family:"Material Icons";font-style:normal;font-weight:400;src:url(https://example.com/MaterialIcons-Regular.eot);src:local("Material Icons"),local("MaterialIcons-Regular"),url(https://example.com/MaterialIcons-Regular.woff2) format("woff2"),url(https://example.com/MaterialIcons-Regular.woff) format("woff"),url(https://example.com/MaterialIcons-Regular.ttf) format("truetype")}body{margin:0;font-family:-apple-system,BlinkMacSystemFont,"Segoe UI","Roboto","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}.sidebar{max-width:300px;width:100vw;height:100vh;position:relative;top:30%;background-color:#000}.logosidebar{text-align:center;margin-top:50px}.allMenu{position:relative;top:20%}.nameMenu{color:#fff;text-align:center;cursor:pointer}.nameMenu:hover{background-color:red}.nameMenu a{color:#fff}.nameMenu a:hover{color:#fff}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

